.screen-reg {
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../img/screen-reg-desktop@1x.jpg") no-repeat center center;
  background-size: cover;
  height: 100vh;
  z-index: 2;
  overflow: hidden;

  @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
    background-image: url("../img/screen-reg-desktop@2x.jpg");
  }

  @media (max-width: $tablet-width - 1) {
    background-image: url("../img/screen-reg-mobile@1x.jpg");

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-reg-mobile@2x.jpg");
    }
  }
}

.screen-reg__feedback {
  width: 1176px;
  height: 506px;
  margin-top: -16px;
  background: url("../img/reg-form-desktop@1x.png") no-repeat center center;
  filter: drop-shadow(0 16px 40px rgba(0, 0, 0, 0.25));

  @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
    background-image: url("../img/reg-form-desktop@2x.png");
    background-size: 1176px 506px;
  }

  @media (max-width: $desktop-width - 1) {
    width: 758px;
    height: 326px;
    background-size: cover;
  }

  @media (max-width: $tablet-width - 1) {
    width: 462px;
    height: 601px;
    margin-right: -71px;
    margin-left: -71px;
    margin-top: 5%;
    margin-bottom: 0;
    background: url("../img/reg-form-mobile@1x.png") no-repeat center center;
    background-size: cover;

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/reg-form-mobile@2x.png");
    }
  }

  @media (max-width: 399px) {
    margin-top: 0;
    margin-bottom: -14%;
  }
}

.screen-reg__text {
  margin: 80px 0 53px 127px;
  font-size: 40px;
  line-height: 48px;
  font-weight: 700;
  color: white;

  @media (max-width: $desktop-width - 1) {
    margin: 50px 0 25px 75px;
    font-size: 27px;
    line-height: 32px;
  }

  @media (max-width: $tablet-width - 1) {
    margin: 56px 0 24px 92px;
    font-size: 32px;
    line-height: 38px;
  }
}

.screen-reg__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1004px;
  margin: 0 auto;

  @media (max-width: $desktop-width - 1) {
    width: 648px;
  }

  @media (max-width: $tablet-width - 1) {
    flex-direction: column;
    align-items: center;
    width: 462px;
  }
}

.screen-reg__field {
  outline: none;
  margin-bottom: 32px;
  padding: 0;
  filter: drop-shadow(0 6px 13px rgba(0, 0, 0, 0.2));

  &--name {
    width: 473px;
    height: 86px;
    background: url("../img/reg-input-1-desktop.svg") no-repeat center center;

    @media (max-width: $desktop-width - 1) {
      width: 305px;
      margin-bottom: 0;
      background-size: 305px auto;
    }

    @media (max-width: $tablet-width - 1) {
      width: 292px;
      height: 71px;
      margin-left: -13px;
      margin-bottom: 18px;
      background: url("../img/reg-input-1-mobile.svg") no-repeat center center;
    }
  }

  &--email {
    width: 482px;
    height: 82px;
    margin: 2px 15px 0 0;
    background: url("../img/reg-input-2-desktop.svg") no-repeat center center;

    @media (max-width: $desktop-width - 1) {
      width: 305px;
      margin-bottom: 0;
      background-size: 305px auto;
    }

    @media (max-width: $tablet-width - 1) {
      width: 296px;
      height: 72px;
      margin-left: 6px;
      margin-bottom: 22px;
      background: url("../img/reg-input-2-mobile.svg") no-repeat center center;
    }
  }

  &--status {
    width: 451px;
    height: 74px;
    background: url("../img/reg-input-3-desktop.svg") no-repeat center center;

    @media (max-width: $desktop-width - 1) {
      width: 305px;
      margin-bottom: 0;
      background-size: 305px auto;
    }

    @media (max-width: $tablet-width - 1) {
      width: 296px;
      height: 68px;
      margin-left: -9px;
      margin-bottom: 40px;
      background: url("../img/reg-input-3-mobile.svg") no-repeat center center;
    }
  }
}

.screen-reg__input {
  height: 60px;
  width: 350px;
  margin: 12px 0 0 36px;
  padding: 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  color: #969696;
  outline: none;
  border: none;
  background-color: transparent;

  @media (max-width: $desktop-width - 1) {
    width: 255px;
    height: 30px;
    margin-top: 25px;
  }

  @media (max-width: $tablet-width - 1) {
    width: 255px;
    height: 20px;
    margin-top: 28px;
    margin-left: 29px;
    font-size: 16px;
    line-height: 19px;
  }

  &--email {
    margin-top: 10px;
    margin-left: 40px;

    @media (max-width: $desktop-width - 1) {
      margin-top: 25px;
    }

    @media (max-width: $tablet-width - 1) {
      margin-top: 28px;
      margin-left: 28px;
    }
  }

  &--status {
    margin-top: 7px;
    margin-left: 36px;

    @media (max-width: $desktop-width - 1) {
      margin-top: 20px;
    }

    @media (max-width: $tablet-width - 1) {
      margin-top: 25px;
      margin-left: 28px;
    }
  }

  &::placeholder {
    padding: 0;
    margin: 0;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    color: #969696;

    @media (max-width: $desktop-width - 1) {
      font-size: 16px;
      line-height: 19px;
      font-weight: 400;
    }
  }

  &::-webkit-input-placeholder {
    padding: 0;
    margin: 0;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    color: #969696;

    @media (max-width: $desktop-width - 1) {
      font-size: 16px;
      line-height: 19px;
      font-weight: 400;
    }
  }

  &::-ms-input-placeholder {
    padding: 0;
    margin: 0;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    color: #969696;

    @media (max-width: $desktop-width - 1) {
      font-size: 16px;
      line-height: 19px;
      font-weight: 400;
    }
  }
}

.screen-reg__btn {
  position: relative;
  width: 463px;
  height: 80px;
  margin: 0 39px 0 0;
  padding-right: 54px;
  background: url("../img/reg-button-desktop.svg") no-repeat center center;
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  color: white;
  outline: none;
  border: none;
  cursor: pointer;
  transition: background-image $transition-duration;
  filter: drop-shadow(0 6px 13px rgba(0, 0, 0, 0.2));

  &:hover,
  &:focus {
    background-image: url("../img/reg-button-desktop-press.svg");
  }

  &::after {
    position: absolute;
    content: "";
    top: 28px;
    right: 149px;
    width: 36px;
    height: 23px;
    background: url("../img/button-arrow.svg") no-repeat center center;
  }

  @media (max-width: $desktop-width - 1) {
    width: 305px;
    margin: 0 25px 0 0;
    background-size: 305px auto;
    font-size: 16px;
    line-height: 19px;

    &::after {
      top: 34px;
      right: 105px;
      width: 24px;
      height: 15px;
      background-size: cover;
    }
  }

  @media (max-width: $tablet-width - 1) {
    width: 301px;
    margin-left: 22px;
    font-size: 24px;
    line-height: 29px;
    background: url("../img/reg-button-mobile.svg") no-repeat center center;

    &:hover,
    &:focus {
      background-image: url("../img/reg-button-mobile-press.svg");
    }

    &::after {
      top: 28px;
      right: 67px;
      width: 36px;
      height: 23px;
    }
  }
}

.screen-reg__copyright {
  position: absolute;
  left: 50%;
  bottom: 1%;

  transform: translateX(-50%);

  padding-right: 45px;

  font-family: "Istok Web", "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.02em;
  color: #ffffff;

  @media (max-width: $tablet-width - 1) {
    display: none;
  }
}

.screen-reg__copyright-link {
  display: block;

  position: absolute;
  right: 0;
  bottom: 1px;

  width: 40px;
  height: 16px;

  background-image: url("../img/binn.svg");
  background-repeat: no-repeat;
  background-size: cover;

  transition: opacity $transition-duration;

  &:focus {
    outline: none;
  }

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}
