body {
  padding: 0;
  margin: 0 auto;
  max-width: $desktop-max-width;
  font-family: "Museo Sans Cyrl", "Helvetica", sans-serif;
  font-size: 26px;
  line-height: 26px;
  font-weight: 500;
}

img {
  max-width: 100%;
}

.swiper-container {
  width: 100%;
  height: 100vh;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
}
