.progress {
  opacity: 1;
  transition: opacity $transition-duration;

  position: fixed;
  top: 50%;
  right: 30px;
  z-index: 3;

  transform: translateY(-50%);

  display: flex;
  flex-direction: column;
  align-items: flex-end;

  width: 70px;

  @media (max-width: $tablet-width - 1) {
    display: none;
  }

  &__item {
    display: block;

    margin-bottom: 20px;
    width: 32px;
    height: 2px;

    opacity: 0.5;

    background-color: #ffffff;

    transition-property: width, opacity;
    transition-duration: 0.4s;

    &:last-child {
      margin-bottom: 0;
    }

    &--active {
      opacity: 1;
      width: 100%;
    }
  }

  &--hidden {
    opacity: 0;
  }
}