.screen-lock {
  display: flex;
  justify-content: center;
  background: url("../img/screen-lock-desktop@1x.jpg") no-repeat top center;
  background-size: cover;
  height: 100vh;
  z-index: 2;
  overflow: hidden;

  @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
    background-image: url("../img/screen-lock-desktop@2x.jpg");
  }

  @media (max-width: $tablet-width - 1) {
    background-image: url("../img/screen-lock-mobile@1x.jpg");

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-lock-mobile@2x.jpg");
    }
  }

  @media (max-width: 399px) {
    align-items: flex-end;
  }
}

.screen-lock__error {
  display: none;

  position: absolute;
  top: 240px;
  left: 110px;

  margin: 0;

  color: #ce2028;
  font-size: 20px;
  line-height: 1em;

  @media (max-width: $tablet-width - 1) {
    top: 197px;
  }

  &--visible {
    display: block;
  }
}

.screen-lock__feedback {
  position: relative;

  margin-top: auto;
  width: 638px;
  height: 423px;
  background: url("../img/lock-form-desktop@1x.png") no-repeat center center;
  filter: drop-shadow(0 16px 40px rgba(0, 0, 0, 0.25));

  @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
    background-image: url("../img/lock-form-desktop@2x.png");
    background-size: 638px 423px;
  }

  @media (max-width: $tablet-width - 1) {
    width: 479px;
    height: 392px;
    margin-right: -80px;
    margin-left: -80px;
    background: url("../img/lock-form-mobile@1x.png") no-repeat center center;

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/lock-form-mobile@2x.png");
      background-size: 479px 392px;
    }
  }

  @media (max-width: 399px) {
    margin-bottom: -68px;
  }
}

.screen-lock__text {
  margin: 68px 0 40px 110px;
  font-size: 40px;
  line-height: 48px;
  font-weight: 700;
  color: white;

  @media (max-width: $tablet-width - 1) {
    margin: 56px 0 33px 107px;
    font-size: 32px;
    line-height: 38px;
  }
}

.screen-lock__field {
  width: 442px;
  height: 82px;
  margin: 0 0 34px 95px;
  padding: 0;
  background: url("../img/lock-input-desktop.svg") no-repeat center center;
  outline: none;
  filter: drop-shadow(0 6px 13px rgba(0, 0, 0, 0.2));

  @media (max-width: $tablet-width - 1) {
    width: 296px;
    height: 70px;
    margin: 0 0 23px 94px;
    background: url("../img/lock-input-mobile.svg") no-repeat center center;
  }
}

.screen-lock__input {
  box-sizing: border-box;
  width: 100%;
  height: 71px;
  padding: 9px 36px 0 41px;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  color: #969696;
  outline: none;
  border: none;
  background-color: transparent;

  @media (max-width: $tablet-width - 1) {
    height: 49px;
    padding: 25px 32px 0 36px;
  }

  &::placeholder {
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    color: #969696;
  }

  &::-webkit-input-placeholder {
    padding: 0;
    margin: 0;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    color: #969696;
  }

  &::-ms-input-placeholder {
    padding: 0;
    margin: 0;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    color: #969696;
  }
}

.screen-lock__btn {
  position: relative;
  width: 423px;
  height: 80px;
  margin-left: 90px;
  padding-right: 54px;
  background: url("../img/lock-button-desktop.svg") no-repeat center center;
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  color: white;
  outline: none;
  border: none;
  cursor: pointer;
  transition: background-image $transition-duration;
  filter: drop-shadow(0 6px 13px rgba(0, 0, 0, 0.2));

  &:hover,
  &:focus {
    background-image: url("../img/lock-button-desktop-press.svg");
  }

  &::after {
    position: absolute;
    content: "";
    top: 28px;
    right: 149px;
    width: 36px;
    height: 23px;
    background: url("../img/button-arrow.svg") no-repeat center center;
  }

  @media (max-width: $tablet-width - 1) {
    width: 301px;
    margin-left: 89px;
    background: url("../img/lock-button-mobile.svg") no-repeat center center;

    &:hover,
    &:focus {
      background-image: url("../img/lock-button-mobile-press.svg");
    }

    &::after {
      right: 88px;
    }
  }
}
