.screen {
  position: relative;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  overflow: hidden;
  height: 100vh;
  z-index: 1;

  &--1 {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-image: url("../img/screen-1-desktop@1x.jpg");
    // z-index: 2;

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-1-desktop@2x.jpg");
    }

    @media (max-width: $tablet-width - 1) {
      justify-content: flex-start;
      background-position: center bottom;
      background-image: url("../img/screen-1-mobile@1x.jpg");

      @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
        background-image: url("../img/screen-1-mobile@2x.jpg");
      }
    }
  }

  &--2 {
    background-position: center bottom;
    background-image: url("../img/screen-2@1x.jpg");
    // z-index: 1;

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-2@2x.jpg");
    }
  }

  &--3 {
    background-position: center center;
    background-image: url("../img/screen-3@1x.jpg");

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-3@2x.jpg");
    }
  }

  &--4 {
    background-image: url("../img/screen-4@1x.jpg");

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-4@2x.jpg");
    }
  }

  &--5 {
    background-position: center 20%;
    background-image: url("../img/screen-5@1x.jpg");

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-5@2x.jpg");
    }
  }

  &--6 {
    background-position: center 30%;
    background-image: url("../img/screen-6@1x.jpg");

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-6@2x.jpg");
    }
  }

  &--7 {
    background-image: url("../img/screen-7@1x.jpg");

    @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
      background-image: url("../img/screen-7@2x.jpg");
    }
  }

  &--desktop {
    @media (max-width: $tablet-width - 1) {
      display: none;
    }
  }
}

.screen__wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 900px;
  height: 71.5%;
  margin-left: -16%;

  @media (max-width: $desktop-width - 1) {
    width: 700px;
    height: 90%;
    margin: 0 auto;
  }

  @media (max-width: $tablet-width - 1) {
    width: 100%;
    margin: 0 16px;
    height: 91.8%;
  }
}

.screen__title {
  margin: 0;
  font-size: 81px;
  line-height: 81px;
  font-weight: 500;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.15);

  @media (max-width: $tablet-width - 1) {
    font-size: 48px;
    line-height: 48px;
  }
}

.screen__title-numbers {
  margin: auto 0 0 -3px;
  font-size: 200px;
  line-height: 239px;
  font-weight: 600;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.15);

  &--desktop {
    @media (max-width: $desktop-width - 1) {
      display: none;
    }
  }

  &--tablet {
    margin: 0 0 auto 0;
    font-size: 100px;
    line-height: 120px;
    font-weight: 900;

    @media (min-width: $desktop-width) {
      display: none;
    }
  }
}

.screen__title-jubilee {
  margin-left: 114px;
}

.screen__invite {
  @media (max-width: $desktop-width - 1) {
    margin: 0 auto;
  }

  @media (max-width: $tablet-width - 1) {
    margin: 0;
    margin-bottom: 60px;
  }
}

.screen__text {
  margin: 0;
  font-size: 22px;
  line-height: 27px;
  text-shadow: 0 2px 2px rgba(0, 0, 0, 0.32);

  @media (max-width: $desktop-width - 1) {
    width: 350px;
    margin: 0 auto 20px auto;
    font-size: 30px;
    line-height: 36px;
    text-align: center;
  }

  @media (max-width: $tablet-width - 1) {
    width: auto;
    margin: 0 -16px;
    padding: 45px 16px 16px;
    font-size: 24px;
    line-height: 26px;
    font-weight: 500;
    letter-spacing: 0.25px;
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 100%);
  }
}

.screen__main-button {
  display: block;
  width: 437px;
  height: 77px;
  margin: 15px 0 0 -3px;
  padding: 25px 94px;
  box-sizing: border-box;
  font-size: 24px;
  line-height: 29px;
  font-weight: 600;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  background-color: #fea844;
  color: #ffffff;
  text-decoration: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color $transition-duration;

  &:hover,
  &:focus {
    background-color: #fe922c;
  }

  @media (max-width: $tablet-width - 1) {
    position: relative;
    display: block;
    width: auto;
    height: 64px;
    margin: 0 0 -7px 0;
    padding: 20px 40px;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    box-shadow: 0 500px 0 500px rgba(0, 0, 0, 0.3);
  }
}

.screen__scroll {
  position: absolute;
  content: "";
  bottom: 28px;
  left: 50%;
  transform: translateX(-50%);
  width: 84px;
  height: 65px;
  padding-top: 45px;
  box-sizing: border-box;
  font-size: 16px;
  line-height: 19px;
  background-image: url("../img/mouse@1x.png");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: 25px 40px;

  @media (max-width: $desktop-width - 1) {
    display: none;
  }

  @media (min-device-pixel-ratio: $retina-dppx), (min-resolution: $retina-dpi) {
    background-image: url("../img/mouse@2x.png");
  }
}

.screen__scale {
  position: absolute;
  content: "";
  top: 39%;
  right: 24px;
  width: 86px;
  height: 172px;

  &--1 {
    top: 46.3%;
    background: url("../img/scale-1.svg") no-repeat 0 0;
  }

  &--2 {
    background: url("../img/scale-2.svg") no-repeat 0 0;
  }

  &--3 {
    background: url("../img/scale-3.svg") no-repeat 0 0;
  }

  &--4 {
    background: url("../img/scale-4.svg") no-repeat 0 0;
  }

  &--5 {
    background: url("../img/scale-5.svg") no-repeat 0 0;
  }

  &--6 {
    background: url("../img/scale-6.svg") no-repeat 0 0;
  }

  &--7 {
    background: url("../img/scale-7.svg") no-repeat 0 0;
  }

  @media (max-width: $desktop-width - 1) {
    display: none;
  }
}

.screen__button {
  position: fixed;
  top: 54.8%;
  left: -88px;
  width: 228px;
  height: 54px;
  padding-top: 16px;
  box-sizing: border-box;
  font-size: 18px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  background: rgba(254, 168, 68, 0.75);
  border-radius: 0 0 4px 4px;
  transform: rotate(-90deg);
  cursor: pointer;

  &:hover,
  &:focus {
    background: rgba(254, 168, 68, 1);
  }
}
