.icon {
  cursor: pointer;

  display: inline-flex;
  align-items: center;
  // justify-content: center;

  position: absolute;

  box-sizing: border-box;
  min-width: 144px;
  max-width: 144px;
  padding: 24px 40px;
  padding-left: 100px;
  height: 144px;

  transition-property: max-width, padding;
  transition-duration: 0.8s, 0.4s;
  white-space: nowrap;

  background-color: transparent;
  background-repeat: no-repeat;

  &--car {
    top: 10%;
    left: 12%;

    background-image: url("../img/icon-car.svg");
    background-size: 88px auto;
    background-position: 28px 46px;

    // &:hover {
    //   top: 10.1%;
    //   left: 11.8%;
    // }

    @media (max-width: $desktop-width - 1) {
      top: 20%;

      &:hover {
        top: 20%;
      }
    }
  }

  &--target {
    top: 11.8%;
    left: 9%;

    background-image: url("../img/icon-target.svg");
    background-size: 80px auto;
    background-position: 32px 32px;
  }

  &--fish {
    top: 9.9%;
    left: 12.1%;

    background-image: url("../img/icon-fish.svg");
    background-size: 76px auto;
    background-position: 34px 32px;
  }

  &--flag,
  &--ball {
    top: 10.6%;
    left: 15.8%;

    // &:hover {
    //   left: 15.9%;
    // }

    @media (max-width: $desktop-width - 1) {
      top: 28%;
      left: 4%;

      &:hover {
        left: 4%;
      }
    }
  }

  &--ball {
    background-image: url("../img/icon-ball.svg");
    background-size: 80px auto;
    background-position: 32px 32px;
  }

  &--flag {
    background-image: url("../img/icon-flag.svg");
    background-size: 80px auto;
    background-position: 32px 43px;
  }

  &--girl {
    left: 40.2%;
    bottom: 10.7%;

    background-image: url("../img/icon-girl.svg");
    background-size: 76px auto;
    background-position: 34px 27px;

    // &:hover {
    //   background-position: 34px 32px;
    // }

    @media (max-width: $desktop-width - 1) {
      top: 10%;
      bottom: auto;
    }
  }

  &:hover {
    max-width: 100%;
    padding-left: 140px;
    padding-right: 57px;

    .icon__pulse {
      border-radius: 200px;
      animation: none;
    }

    .icon__info {
      width: 100%;
      opacity: 1;

      &::before {
        opacity: 1;
      }
    }
  }

  .icon__pulse {
    // content: "";

    display: block;

    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;
    border-radius: 250px;

    transition: border-radius 0.4s;

    background-color: #ffffff;
    animation: pulse 2s infinite 0.8s;
  }

  &::after {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    border-radius: 200px;

    box-shadow: 0 6px 13px rgba(0, 0, 0, 0.2);
  }

  &__info {
    position: relative;

    margin: 0;
    width: 0;
    padding-left: 56px;
    padding-bottom: 10px;
    overflow: hidden;

    opacity: 0;

    transition-property: width, opacity;
    transition-duration: 0.8s;

    font-size: 28px;
    line-height: 28px;

    &::before {
      content: "";

      position: absolute;
      top: 50%;
      left: 12px;

      opacity: 0;

      transform: translateY(-50%);

      width: 4px;
      height: 104px;

      background-color: #ccd6dd;
      border-radius: 200px;
    }
  }

  &__title,
  &__text {
    display: block;
  }

  &__title {
    font-size: 64px;
    line-height: 77px;
    font-weight: 800;
    color: #ff971d;

    @media (max-width: $desktop-width - 1) {
      font-size: 42px;
      line-height: 51px;
    }
  }

  &__text {
    @media (max-width: $desktop-width - 1) {
      font-size: 19px;
      line-height: 19px;
    }
  }
}
