.item--plane.animated {
  animation: plane-screen-1 3s linear forwards;
}

.item--car.animated {
  animation: car-screen-1 2s 2s ease-out forwards;
}

.item__phrase--2.animated {
  animation: fade-in 1s 4.5s linear forwards;
}

.item--hunter.animated {
  opacity: 1;
  animation: hunter-screen-2 6s forwards;
}

.item--animals.animated {
  animation: fade-in 2s 1s linear forwards;
}

.item--boar.animated {
  animation: animal-move-screen-2 1s 2s ease-out forwards;
}

.item--fox.animated {
  animation: animal-move-screen-2 1s 2.5s ease-out forwards;
}

.item--deer.animated {
  animation: animal-move-screen-2 1s 3s ease-out forwards;
}

.item__phrase--3.animated {
  animation: fade-in 1s 4.5s linear forwards;
}

.item__phrase--7-1.animated {
  animation: fade-in 1s 0.5s linear forwards;
}

.item__phrase--7-2.animated {
  animation: fade-in 1s 2s linear forwards;
}

.item--fisherman.animated {
  animation: fisherman-screen-3 3s 1s ease-in-out forwards;
}

.item--water-boar.animated {
  animation: animal-screen-3 2s ease infinite;
}

.item--water-fox.animated {
  animation: animal-screen-3 2s ease infinite;
}

.item__phrase--4-1.animated {
  animation: fade-in 1s 2.5s linear forwards;
}

.item--fish.animated {
  animation: fishes-screen-3 4s 1.5s ease-in-out forwards;
}

.item__phrase--4-2.animated {
  animation: fade-in 1s 6s linear forwards;
}

.item--football-fan-1.animated {
  animation: auto-screen-4 3s ease-out forwards;
}

.item__phrase--5-1.animated {
  animation: fade-in 1s 3.5s linear forwards;
}

.item__phrase--5-2.animated {
  animation: fade-in 1s 4.5s linear forwards;
}

.item--footballer-1-1.animated {
  animation: footballer-1-screen-4 1.5s 1s ease-out forwards;
}

.item--footballer-1-2.animated {
  animation: footballer-2-screen-4 1.5s 1s ease-out forwards;
}

.item--football-fan-2.animated {
  animation: auto-screen-5 3s ease-out forwards;
}

.item--footballer-2-1.animated {
  animation: footballer-1-screen-5 1.5s 1s ease-out forwards;
}

.item--footballer-2-2.animated {
  animation: footballer-2-screen-5 1.5s 1.1s ease-out forwards;
}

.item__phrase--6.animated {
  animation: fade-in 1s 3s linear forwards;
}

.item--software.animated {
  animation: software-screen-5 2s 1.2s ease-out forwards;
}

@keyframes plane-screen-1 {
  0% {
    transform: translateX(100%) rotate(10deg);
    opacity: 0;
  }

  100% {
    transform: translateX(0) rotate(0);
    opacity: 1;
  }
}

@keyframes car-screen-1 {
  0% {
    transform: translateX(100vw);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fade-in-up {
  0% {
    transform: translateY(100vw);
  }

  100% {
    transform: none;
  }
}

@keyframes hunter-screen-2 {
  0% {
    opacity: 0;
    transform: translateY(-60%) translateX(20vw) scale(0.3);
  }

  5% {
    opacity: 1;
    transform: translateY(-60%) translateX(20vw) scale(0.3);
  }

  25% {
    opacity: 1;
    transform: translateY(-60%) translateX(20vw) scale(0.3);
  }

  30% {
    opacity: 0;
    transform: translateY(-60%) translateX(20vw) scale(0.3);
  }

  35% {
    opacity: 0;
    transform: translateY(-50%) translateX(40vw) scale(-0.5, 0.5);
  }

  60% {
    opacity: 1;
    transform: translateY(-50%) translateX(40vw) scale(-0.5, 0.5);
  }

  65% {
    opacity: 0;
    transform: translateY(-50%) translateX(40vw) scale(-0.5, 0.5);
  }

  70% {
    opacity: 0;
    transform: none;
  }

  100% {
    opacity: 1;
  }
}

@keyframes animal-move-screen-2 {
  0% {
    transform: none;
  }

  100% {
    transform: translateX(10%);
  }
}

@keyframes fisherman-screen-3 {
  0% {
    transform: none;
  }

  50% {
    transform: rotate(10deg);
  }

  60% {
    transform: rotate(-10deg);
  }

  100% {
    transform: none;
  }
}

@keyframes animal-screen-3 {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(0);
  }
}

@keyframes fishes-screen-3 {
  0% {
    opacity: 0;
    transform: translateY(100vh) translateX(50vw);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes auto-screen-4 {
  0% {
    opacity: 0;
    transform: translateX(100vw);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes footballer-1-screen-4 {
  0% {
    transform: translateX(20%) translateY(-20%);
