.aside-button {
  opacity: 0;

  position: fixed;
  top: 50%;
  left: 0;
  z-index: 3;

  transform: translateY(-50%) rotate(-180deg);

  display: block;
  width: 54px;
  height: 228px;
  // padding-right: 16px;
  box-sizing: border-box;
  font-size: 18px;
  line-height: 22px;
  writing-mode: vertical-rl;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  background-color: rgba(254, 168, 68, 0.75);
  border-radius: 4px 0 0 4px;
  cursor: pointer;

  transition-property: background-color, opacity;
  transition-duration: $transition-duration;

  @media (max-width: $tablet-width - 1) {
    display: none;
  }

  &:hover,
  &:focus {
    background-color: rgba(254, 168, 68, 1);
  }

  &--visible {
    opacity: 1;
  }

  &__text {
    display: block;
    margin-right: 14px;
  }
}
